
    *{
        margin:0;
        padding:0;
        box-sizing: border-box;
        font-family: 'Montserrat',sans-serif;
       
    }
    body{
        background: grey;
    }
    #header{
        position: fixed;
        top:0;
        background: none;
        display:flex;
        color:white;
        justify-content: space-between;
        width:100%;
        padding:29px 64px 0px 46px;
        font-size: 30px;
        
    }
    
    .left{
      
        span{font-family: 'Sillii Willinn';
        color:red;
        }
       
    font-weight:500;
    }
    .right{
        font-weight: 600;
    }
    .red:hover{color:red;font-weight: 600;}
    .parallax{
  
        height:120vh; 

        background-attachment: fixed;
        
background-position: top;
background-repeat: no-repeat;
background-size: 100% 120vh;
display: flex;
flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    color: white;
    font-weight: 800;
align-items: center;
justify-content: center;
color:white;
h1{text-align:center;font-weight:600;}

}


.parallax1{
    background-image: url('background/Circus.png');
}

.parallax2{
  
    background-image: url('background/MA.png');
    
}
.parallax3{
    background-image: url('background/Museum.png')
}
.parallax4{

    background-image: url('background/University.png')
}
.parallax5{
    background-image: url('background/Factory.png')
    }
    .parallax6{

background-image: url('background/Subway.png')
}

.view{width:200px;
    height:50px;
    border:2px solid rgb(255 255 255);
    background:none;
    color:white;
    font-family: 'Montserrat',sans-serif;
    font-weight: 600;
    font-size: 16px;
    margin-top: 53px;
border-radius: 2px;
display:flex;
align-items: center;
justify-content: center;
}
.view:hover{
    
background: white;
color:black;

}
a{color: white;
text-decoration: none;}
@media (max-width:1500px) {
.parallax{h1{font-size:50px;}} 
 #header{font-size: 20px;}
}
@media (max-width:1000px) {
    .parallax{h1{font-size:30px;}
height:100vh;
    background-size: 100% 100vh;
} 
    
   #header{font-size: 16px;}
    
}

